import React, { useState } from "react";
import LocationAndCategory from "./tabs/LocationAndCategory";
import TopicContent from "./tabs/TopicContent";
import Media from "./tabs/Media";
import Preview from "./tabs/Preview";
import Header from "../../semantics/header";
import Footer from "../../semantics/footer";
import checkMark from "../../assets/images/check-mark.png";
import "./style.scss";

class TopicSuggestion extends React.Component {
  state = {
    page: 1,
    first: {
      form: {
        category: null,
        location: ""
      },

      page: 1,
      inputWarning: false
    }
  };

  firstPageChange = ({ target: { name, value } }) => {
    this.setState(prevState => ({
      ...prevState,
      first: {
        ...prevState.first,
        form: { ...prevState.first.form, [name]: value }
      }
    }));
  };

  onCategoryChange = value => {
    this.setState(prevState => ({
      ...prevState,
      first: {
        ...prevState.first,
        form: { ...prevState.first.form, category: value }
      }
    }));
  };

  moveNextPage = page => {
    if (first.form.category && first.form.location && page === 1) {
      this.setState({
        page: 2,
        checkMark: true
      });
    } else if (!first.form.category || !first.form.location) {
      this.setState({
        inputWarning: true
      });
    }
    // if (page === 2) {
    //   this.setState({
    //     page: 3,
    //     checkMark: true
    //   });
    // }
    // if (page === 3) {
    //   this.setState({
    //     page: 4,
    //     checkMark: true
    //   });
    // }
    // if (page === 4) {
    //   this.setState({
    //     page: 1,
    //     checkMark: true
    //   });
    // }
  };

  render() {
    console.log(this.state.first);
    const {
      first: {
        form: { location, category }
      }
    } = this.state;
    return (
      <>
        <Header />
        <div className="suggest-topic">
          <div className="tabs">
            <ul>
              <li>
                Category and <br />
                location
                {/* {this.state.page === 2 && <img src={checkMark} alt="check-mark" />} */}
              </li>
              <li>
                Topic content
                {/* {this.state.page === 3 && <img src={checkMark} alt="check-mark" />} */}
              </li>
              <li>
                Media
                {/* {this.state.page === 4 && <img src={checkMark} alt="check-mark" />} */}
              </li>
              <li>
                Preview
                {/* {this.state.page === 4 && <img src={checkMark} alt="check-mark" />} */}
              </li>
              ew
            </ul>
          </div>
          <div className="tab-content">
            {this.state.page === 1 ? (
              <LocationAndCategory
                moveNextPage={() => this.moveNextPage(1)}
                onChange={this.firstPageChange}
                onCategoryChange={this.onCategoryChange}
                location={location}
                category={category}
                inputWarning={inputWarning}
              />
            ) : this.state.page === 2 ? (
              <TopicContent moveNextPage={() => this.moveNextPage(2)} />
            ) : this.state.page === 3 ? (
              <Media moveNextPage={() => this.moveNextPage(3)} />
            ) : (
              <Preview moveNextPage={() => this.moveNextPage(4)} />
            )}
          </div>
        </div>
        <Footer />
      </>
    );
  }
}

export default TopicSuggestion;
